import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';

import { HeaderComponent } from './header.component';

// Each additional locale translation needs to be imported separately
// e.g `import { translations as translationsFR } from '../locale-fr';`. This
// will ensure `ng-packagr` emits a corresponding file at
// `/lib/src/components/locale-<locale>.d.ts` when building the component
// library.
import { translations as translationsEN } from '../locale-en';

import { MarkdownModule } from 'ngx-md';
import {
  L10nConfig,
  L10nLoader,
  LocalizationModule,
  LocaleValidationModule,
  StorageStrategy,
  ProviderType
} from 'angular-l10n';

const l10nConfig: L10nConfig = {
  locale: {
    languages: [
      { code: 'en', dir: 'ltr' }
    ],
    defaultLocale: { languageCode: 'en', countryCode: 'US' },
    currency: 'USD',
    storage: StorageStrategy.Cookie
  },
  translation: {
    translationData: [
      // Each additional locale translation needs to be added here
      // e.g. `{ languageCode: 'fr', data: translationsFR }`
      { languageCode: 'en', data: translationsEN },
    ],
    caching: true,
    missingValue: 'No key',
    composedKeySeparator: '.',
    i18nPlural: true
  }
};

export * from './header.component';

@NgModule({
  imports: [
    CommonModule,
    MarkdownModule,
    HttpClientModule,
    LocalizationModule.forRoot(l10nConfig),
    LocaleValidationModule.forRoot(),
  ],
  declarations: [ HeaderComponent ],
  exports: [ HeaderComponent ]
})
export class HeaderModule {
  constructor(public l10nLoader: L10nLoader) {
    this.l10nLoader.load();
  }
}
